<template>
  <div class="progress-bar">
    <div class="played-progress" :style="{ transform: 'scaleX(' + percentagePlayed + ')' }"></div>
  </div>
</template>

<script>
export default {
  name: 'VideoProgress',

  props: {
    duration: {
      type: Number,
      default: 0
    },
    currentTime: {
      type: Number,
      default: 0
    }
  },

  computed: {
    percentagePlayed () {
      return this.currentTime / this.duration
    }
  }
}
</script>

<style scoped lang="scss">
@import "@/styles/_variables.scss";

.progress-bar {
  position: absolute;
  bottom: 1px;
  left: 0;
  width: 100%;
  height: 2px;
  z-index: 5;

  .played-progress {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    bottom: 0;
    left: 0;
    transition: transform .25s;
    transform-origin: 0 0;
    transform: scaleX(0);
    background-color: $primary-color;
  }
}
</style>
